import React from "react";
import bannerConocenos from "../Components/Assets/banner_conocenos.png"
import './CSS/Conocenos.css'
import conocenosLogo from '../Components/Assets/conocenosLogo.png'


const Conocenos = () => {
    return(
        <div className="conocenos">
            <img className="conocenosBanner" src={bannerConocenos} alt="Banner de Concenos" />
            <div className="conocenosGrid">
                <img className="conocenosImg" src={conocenosLogo} alt="Logotipo de Concenos" />
                <div className="accordion">
                    {articles.map(article => (
                        <Article key={article.id} article={article} />
                    ))}
                </div>
            </div>
        </div>
    )
}

const articles = [
    {
        id: "article1",
        title: "Nuestra Historia",
        content: [
            {type: "paragraph", data: "Empezamos en el negocio de las pastas para clutch hace 15 aos, de la mano de la marca VALEO, defendiendo la calidad en la lnea, siempre manejando la fibra de vidrio en combinacin con el cobre, a la salida de la marca VALEO del pas, seguimos ofreciendo a nuestros clientes la misma calidad en otras marcas como es TERMOLITE y FRAS-LE. "},
            { type: "paragraph", data: "Viendo la necesidad del mercado de contar con una pasta de calidad en la reparacin de los embragues para los vehculos que se comercializan en Mxico, es que naci VAFRI, la pasta para clutch que est elaborada en 4 formulaciones diferentes, todo esto para que el cliente tenga la mejor solucin a su necesidad."}
        ]
    },
    {
        id: "article2",
        title: "Misin",
        content: [
            { type: "paragraph", data: "Ser lder de soluciones integrales en componentes para embragues a nivel Nacional, proporcionando a nuestros clientes un servicio de gran valor, incorporando al mercado pastas para clutch en diferentes frmulas y otros productos con excelente calidad, a travs del desarrollo tecnolgico que nuestros clientes requieren."}
        ]
    },
    {
        id: "article3",
        title: "Visin",
        content: [
            { type: "paragraph", data: "Consolidarnos como lideres en el rango, expandiendo nuestra penetracin en el mercado nacional sin descuidar la atencin, servicio y calidad que ofrecemos da a da."}
        ]
    },
    {
        id: "article4",
        title: "Valores",
        content: [
            { type: "paragraph", data: "Nuestros valores son los que nos ayudan a seguir nuestra misin y visin, por lo cual da a da los respetamos y usamos." },
            { type: "list", data: ["Trabajo en equipo", "Crecimiento", "Responsabilidad Social", "Honestidad", "Calidad"] }
        ]
    },
];

const Article = ({ article }) => {
    return (
        <article>
            <input id={article.id} type="radio" name="articles" />
            <label htmlFor={article.id}>
                <h2>{article.title}</h2>
            </label>
            <div>
                {Array.isArray(article.content) ? article.content.map((item, index) => {
                    if (item.type === "paragraph") {
                        return <p key={index}>{item.data}</p>;
                    } else if (item.type === "list") {
                        return (
                            <ul key={index}>
                                {item.data.map((listItem, liIndex) => (
                                    <li key={liIndex}>{listItem}</li>
                                ))}
                            </ul>
                        );
                    }
                    return null;
                }) : <p>Error: Article content is not formatted correctly.</p>}
            </div>
        </article>
    );
};

export default Conocenos;
